import { filterStrings } from "./filterStrings";
export function customList(toParse, opt) {
    let parsed;
    const parsedArray = filterStrings(toParse);
    if (parsedArray?.length) {
        parsed = `${opt?.start ?? ""}${parsedArray.join(opt?.join ?? "")}${opt?.end ?? ""}`;
    }
    else {
        parsed = undefined;
    }
    return parsed;
}
