import { writable } from "svelte/store";
import { beforeOrAfter } from "./domBeforeOrAfter.js";
export function createComponentsGroupStore() {
    const { subscribe, set, update } = writable([]);
    return {
        subscribe,
        update,
        set,
        async registerItem(item) {
            update((items) => {
                items.push(item);
                if (typeof window !== "undefined") {
                    items.sort((a, b) => beforeOrAfter(a.dom, b.dom));
                }
                return [...items];
            });
        },
        async unregisterItem(item) {
            update((items) => {
                const index = items.indexOf(item);
                if (~index) {
                    items = items.slice(0, index).concat(items.slice(index + 1));
                }
                return [...items];
            });
        },
        async updateItem(item, newContext) {
            if (newContext) {
                Object.assign(item, newContext);
            }
            update((items) => {
                return [...items];
            });
        },
    };
}
