import { MDCRipple, MDCRippleFoundation, } from "@material/ripple";
export class SMUIRipple {
    root;
    active = false;
    ripple;
    #unbounded = false;
    constructor(root, { classForward, keyboardEvents }) {
        this.root = root;
        let classList = [];
        if (keyboardEvents) {
            this.root.addEventListener("keydown", (event) => {
                if (isSubmitKey(event))
                    this.active = true;
            });
            this.root.addEventListener("keyup", (event) => {
                if (isSubmitKey(event))
                    this.active = false;
            });
        }
        const adapter = {
            ...MDCRipple.createAdapter(this),
            addClass: (className) => {
                const idx = classList.indexOf(className);
                if (idx === -1) {
                    this.root.classList.add(className);
                    classList.push(className);
                    if (classForward) {
                        classForward([...classList]);
                    }
                }
            },
            removeClass: (className) => {
                const idx = classList.indexOf(className);
                if (idx !== -1) {
                    this.root.classList.remove(className);
                    classList.splice(idx, 1);
                    if (classForward) {
                        classForward(classList);
                    }
                }
            },
        };
        if (keyboardEvents) {
            adapter.isSurfaceActive = () => this.active;
        }
        const foundation = new MDCRippleFoundation(adapter);
        this.ripple = new MDCRipple(this.root, foundation);
        this.root.addEventListener("focusout", () => {
            this.ripple.deactivate();
        });
    }
    get unbounded() {
        return this.ripple?.unbounded ?? this.#unbounded;
    }
    set unbounded(unbounded) {
        this.ripple.unbounded = unbounded;
        this.#unbounded = unbounded;
    }
    destroy() {
        this.ripple?.destroy();
    }
}
function isSubmitKey(event) {
    const isEnter = event.key === "Enter";
    const isSpace = event.key === "Space";
    return isEnter || isSpace;
}
